.fullscreen-background {
    width: 100%;
    height: 100%;
    overflow: hidden;

    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-position: 50% 50%\9 !important;
    background-size: cover;
}

.overlay-black {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at center, #4440366e 0%, #35373aa8 100%);
    position: absolute;
    overflow: hidden;
}

.overlay-black::after {
    content: "";
    position: absolute;
    width: 200vw;
    height: 200vh;
    left: -50%;
    top: -50%;
    background-image: url(data:image/png;base64,);
    animation: grain 1.5s steps(6) infinite;
}

.grain-img {
    z-index: -1;
}

.overlay-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    line-height: normal;
    z-index: 1;

    .content {
        display: flex;
        position: absolute;
        flex-direction: column;
        height: 100%;
        width: 100%;
        justify-content: center;

        .main-body {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            flex: 1;

            .about-me {
                display: flex;
                flex-direction: row;
                justify-content: center;
                color: #fff;
                width: 100%;
                align-items: center;
                padding: 0px 10px;
                font-size: 1rem;

                .inner-container {
                    display: flex;
                    flex-direction: column;

                    .about-me-wrapper {
                        display: flex;
                        padding-bottom: 10px;
                        align-items: flex-end;
                        justify-content: space-between;

                        .about-me-title {
                            font-size: 20px;
                            font-weight: 600;
                            padding-right: 10px;
                        }
                    }
                }
            }

            .scoll-text {
                display: flex;
                justify-content: center;
                align-items: center;
                letter-spacing: 50px;
                color: rgba(0, 0, 0, 0.4);
                font-size: 10rem;

                .text-wrapper {
                    position: relative;
                    display: inline-block;
                    padding-top: 0.1em;
                    padding-right: 0.05em;
                    padding-bottom: 0.15em;
                    height: 11rem;
                }

                .line {
                    opacity: 0;
                    position: absolute;
                    left: 0;
                    height: 100%;
                    width: 3px;
                    background-color: rgba(0, 0, 0, 0.4);
                    transform-origin: 0 50%;
                }

                .line1 {
                    top: 0;
                    left: 0;
                }

                .letters {
                    .letter {
                        display: inline-block;
                        line-height: 1em;

                        &:last-of-type {
                            letter-spacing: 0 !important;
                        }
                    }
                }
            }
        }

        .links {
            display: flex;
            flex-direction: row;
            justify-content: center;
            padding: 20px;
        }
    }

    .link {
        margin: 15px;

        a {
            color: #ffffffa1;
        }
    }

    .nav-bar {
        display: flex;
        align-self: flex-end;
        color: white;
        font-size: 15px;
        letter-spacing: 2px;
        padding: 1rem;
        text-transform: lowercase;
        z-index: 99999999;

        a {
            color: white;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

@keyframes grain {

    0%,
    100% {
        transform: translate(0, 0);
    }

    10% {
        transform: translate(-5%, -10%);
    }

    30% {
        transform: translate(3%, -15%);
    }

    50% {
        transform: translate(12%, 9%);
    }

    70% {
        transform: translate(9%, 4%);
    }

    90% {
        transform: translate(-1%, 7%);
    }
}

@media screen and (max-width:400px) {
    .overlay-content {
        .content {
            .main-body {
                .scoll-text {
                    letter-spacing: 20px;
                    font-size: 7rem;
                    padding-bottom: 2rem;
                    padding-top: 7rem;

                    .text-wrapper {
                        display: flex;
                        height: 100%;
                        padding: 0 !important;
                    }

                    .letter {
                        display: inline-block;
                        line-height: 1em;
                    }
                }

                .about-me {
                    letter-spacing: normal !important;
                    font-size: 0.8rem;
                    padding: 0 2rem;

                    .inner-container {

                        .about-me-wrapper {
                            display: flex;
                            flex-direction: column;
                            align-items: flex-start;

                            .about-me-title {
                                font-size: 18px;
                                font-weight: 600;
                            }
                        }
                    }
                }
            }

        }
    }
}

